(function (root, factory) {
    if (typeof module !== 'undefined') {
        module.exports = factory();
    } else if (typeof define === 'function' && typeof define.amd !== 'undefined') {
        define(factory);
    } else {
        root.swfobject = factory();
    }
}(this, function () {
    var Sys = {};
    var ua = navigator.userAgent.toLowerCase();
    var s;
    (s = ua.match(/msie ([\d.]+)/)) ? Sys.ie = s[1] :
        (s = ua.match(/firefox\/([\d.]+)/)) ? Sys.firefox = s[1] :
            (s = ua.match(/chrome\/([\d.]+)/)) ? Sys.chrome = s[1] :
                (s = ua.match(/opera.([\d.]+)/)) ? Sys.opera = s[1] :
                    (s = ua.match(/version\/([\d.]+).*safari/)) ? Sys.safari = s[1] : 0;

    var u = navigator.userAgent;
    Sys.isAndroid = !!u.match(/android/ig)
    Sys.isIos = !!u.match(/iphone|ipod/ig)
    Sys.isIpad = !!u.match(/ipad/ig)
    Sys.isIos9 = !!u.match(/OS 9/ig)
    Sys.isYx = !!u.match(/MailMaster_Android/i)
    Sys.isNewsapp = !!u.match(/newsapp/i)
    Sys.isWeixin = (/MicroMessenger/ig).test(u)
    Sys.isYixin = (/yixin/ig).test(u)
    Sys.isQQ = (/qq/ig).test(u)
    Sys.isandroid2_x = Sys.isAndroid && (/android\s?2\./gi).test(u)
    Sys.isIEMobile = !Sys.isAndroid && !Sys.isIos && (/MSIE/gi).test(u)

    Sys.trident = u.indexOf('Trident') > -1; //IE内核
    Sys.presto = u.indexOf('Presto') > -1;//opera内核
    Sys.webKit = u.indexOf('AppleWebKit') > -1;//苹果、谷歌内核
    Sys.gecko = u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1;//火狐内核
    Sys.ios = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);//ios终端
    Sys.android = u.indexOf('Android') > -1;// || u.indexOf('Linux') > -1;//android终端或者uc浏览器
    Sys.iPhone = u.indexOf('iPhone') > -1 || u.indexOf('Mac') > -1; //是否为iPhone或者QQHD浏览器
    Sys.iPad = u.indexOf('iPad') > -1;//是否iPad
    Sys.webApp = u.indexOf('Safari') == -1;//是否web应该程序，没有头部与底部
    Sys.google = u.indexOf('Chrome') > -1;
    Sys.mobile = (Sys.ios || Sys.android);//!!u.match(/AppleWebKit.*Mobile.*/) || !!u.match(/AppleWebKit/);//是否为移动终端
    Sys.language = (navigator.browserLanguage || navigator.language).toLowerCase();
    Sys.qq = u.indexOf('QQ') > -1;
    Sys.detachFlash = (function(){
        if (navigator.mimeTypes.length > 0) {
            //application/x-shockwave-flash是flash插件的名字
            var flashAct = navigator.mimeTypes["application/x-shockwave-flash"];
            return flashAct != null ? flashAct.enabledPlugin != null : false;
        } else if (self.ActiveXObject) {
            try {
                new ActiveXObject('ShockwaveFlash.ShockwaveFlash');
                return true;
            } catch (oError) {
                return false;
            }
        }
    }());
    return Sys
}));


