var utils = {

    launchFullscreen: function launchFullscreen (element) {
        if (element.requestFullscreen) {
            element.requestFullscreen()
        } else if (element.mozRequestFullScreen) {
            element.mozRequestFullScreen()
        } else if (element.msRequestFullscreen) {
            element.msRequestFullscreen()
        } else if (element.webkitRequestFullscreen) {
            element.webkitRequestFullScreen()
        } else if (element.webkitEnterFullScreen) {
            element.webkitEnterFullScreen()
        }
    },
    exitFullscreen: function exitFullscreen (element) {
        if (document.exitFullscreen) {
            document.exitFullscreen()
        } else if (document.msExitFullscreen) {
            document.msExitFullscreen()
        } else if (document.mozCancelFullScreen) {
            document.mozCancelFullScreen()
        } else if (document.webkitExitFullscreen) {
            document.webkitExitFullscreen()
        } else if (element.webkitExitFullscreen) {
            element.webkitExitFullscreen()
        }
    },
    sendStat: function (data) {
        if (!data) {
            console.log('sendStat data ???')
            return false
        }
        // $.get(window.location.origin + '/_.gif', data)
    },
    time: function (s) {
        s = parseInt(s)
        var str = ''
        var H = Math.floor(s / 3600)
        var i = Math.floor(s / 60) % 60
        s = s % 60

        str += (H === 0 ? '' : (H < 10 ? '0' + H + ':' : H + ':'))
        str += (i === 0 ? '00:' : (i < 10 ? '0' + i + ':' : i + ':'))
        str += (s < 10 ? '0' + s : s)
        return str
    },
    getParams: function () {
        var params = {
            account: 'KLive'
        }
            // Document对象数据
        if (document) {
            params.domain = document.domain || ''
            params.url = document.URL || ''
            params.title = document.title || ''
            params.referrer = document.referrer || ''
        }
            // Window对象数据
        if (window && window.screen) {
            params.sh = window.screen.height || 0
            params.sw = window.screen.width || 0
            params.cd = window.screen.colorDepth || 0
        }
            // navigator对象数据
        if (navigator) {
            params.lang = navigator.language || ''
        }
        return params
    }
}

