Player.prototype.setVolume = function (v) {
    return this.action('setVolume', v)
}

Player.prototype.setSeek = function (v) {
    if (!this.vod || !this.vod.length) {
        return
    }
    var duration = v / 100 * this.totalDuration
    var index = 0
    var seek = 0
    if (this.vod.length > 1) {
        for (var i = 0, l = this.vod.length; i < l; i++) {
            var file = this.vod[i]
            if ('duration' in file) {
                duration -= file.duration
            }
            if (duration < 0) {
                index = i
                break
            }
        }
        seek = parseInt(this.vod[index].duration + duration)
    } else {
        seek = parseInt(duration)
    }
    if (this.filesIndex !== index) {
        this.setFilesIndex(index)
        this.action('play', this.vod[index]['mp4'], seek)
    } else {
        this.action('seek', seek)
    }
}

Player.prototype.togglePause = function () {
    this.isPlaying = this.action('togglePause')
}
Player.prototype.stop = function () {
    if (this.isPlaying) {
        this.action('stop')
    }
}

Player.prototype.getTimeBySlider = function (n) {
    var duration = utils.time(n / 100 * this.totalDuration)
    return duration
}

Player.prototype.countTotalDuration = function () {
    if (!this.vod) {
        return
    }
    this.totalDuration = 0
    this.vod.forEach(function (file) {
        if ('duration' in file) {
            this.totalDuration += parseInt(file.duration)
        }
    })
}

Player.prototype.setFilesIndex = function (index) {
    if (index !== this.filesIndex) {
        this.filesIndex = index
        var preDuration = 0
        var vod = this.vod
        while (index--) {
            if ('duration' in vod[index]) {
                preDuration += parseInt(vod[index].duration)
            }
        };
        vod = null
        this.preDuration = preDuration
    }
}

Player.prototype.action = function (action, ...data) {
    if (this.playType === 'flash') {
        try {
            if (data && data.length) {
                return this.swf.__externalCall(action, JSON.stringify(data))
            } else {
                return this.swf.__externalCall(action)
            }
        } catch (e) {
            console.log('action action ::: err', e)
        }
    } else if (this.playType === 'video') {
            //
        switch (action) {
        case 'play':
            this.isPlaying = true
            this.end = false
            this.$front && this.$front.addClass('hide')
            this.$control && this.$control.find('.btn-pause').removeClass('hide').siblings().addClass('hide')
            this.$video.src = data[0]
            this.$video.load()
            if (data[1]) {
                this.$video.currentTime = data[1]
            }
            this.$video.play()
            break
        case 'stop':
        case 'togglePause':
            if (this.isPlaying) {
                this.$video.pause()
                this.$control && this.$control.find('.btn-start').removeClass('hide').siblings().addClass('hide')
                this.isPlaying = false
            } else {
                this.$video.play()
                this.$control && this.$control.find('.btn-pause').removeClass('hide').siblings().addClass('hide')
                this.isPlaying = true
            }
                // break
            return this.isPlaying

        case 'seek':
            this.$video.currentTime = data[0]
            break
        case 'setVolume':
            this.$video.volume = data[0]
            break
        case 'setup':

            break
        default:
            console.log(action, data)
        }
    }
}

Player.prototype.doPlay = function () {
        // console.log("player:::::play do action setup",JSON.stringify(this.opts));
    this.isPlaying = true
    this.action('setup', this.opts)
    if (this.vod) {
        this.setFilesIndex(0)
        this.action('play', this.vod[0]['mp4'])
    } else if (this.file) {
        this.action('play', this.file)
    } else {
        console.log('player:::Error::play do action setup')
    }
}
Player.prototype.play = function () {
    this.addInitLoadEvent(this.doPlay)
}

Player.prototype.fullScreen = function () {
    let $box = document.getElementById(this.box)
    let parent = $box.parentElement || $box.parentNode
    let element = sys.mobile ? $box : parent

    if (!this.isFullScreen) {
        this.isFullScreen = true
        utils.launchFullscreen(element)
    } else {
        utils.exitFullscreen(element)
        this.isFullScreen = false
    }
    $box = null
    parent = null
    element = null
}
Player.prototype.updateSliderInner = function (time) {
    if (!this.playerSlider) {
        return false
    }
    var percent

    if (time > 0) {
            // preDuration = this.preDuration
        percent = (this.preDuration + time) / this.totalDuration
        this.playerSlider && this.playerSlider.value(percent * 100, false)
    }

    percent = null
}
Player.prototype.doTimeLine = function () {
        // var currentTime = this.preDuration + this.$video.currentTime
    this.updateSliderInner(this.$video.currentTime)
        // this.$timeText.innerHTML = '' + timeParse(currentTime) + '/' + timeParse(this.duration)
}
Player.prototype.timeline = function () {
    var self = this
    var now = Date.now()
    clearTimeout(this.timelineTimer)
    if (now - this.timelineLast > 800) {
        this.doTimeLine()
        this.timelineLast = now
    } else {
        this.timelineTimer = setTimeout(function () {
            self.doTimeLine()
        }, 800)
        if (!this.timelineLast) {
            this.timelineLast = now
        }
    }
}
Player.prototype.handlerEnd = function () {
    if (this.vod && this.vod.length) {
        if (this.filesIndex + 1 < this.vod.length) {
            this.setFilesIndex(this.filesIndex + 1)
            this.action('play', this.vod[this.filesIndex]['mp4'])
            return false
        } else {
            this.end = true
            this.setFilesIndex(0)
            clearTimeout(this.timelineTimer)
            this.playerSlider.value(0, false)
        }
    }
    this.$control && this.$control.find('.btn-start').removeClass('hide').siblings().addClass('hide')
}

Player.prototype.appendSWF = function () {
        // To use express install, set to playerProductInstall.swf, otherwise the empty string.
        // var xiSwfUrlStr = '/static/swf/playerProductInstall.swf'
    var flashvars = {
        uuid: this.uuid
    }
    var params = {
        allowscriptaccess: 'sameDomain',
        allowfullscreen: 'true'
    }

    var attributes = {
        'id': this.box,
        'name': this.box,
        'data-uuid': this.uuid
    }
    var self = this
    var swfPath = this.swfPath || '/static/swf/player.swf'
    this.playType = 'flash'
    swfobject.embedSWF(
            swfPath, this.box,
            '100%', '100%',
            '11.1.0', '',
            flashvars, params, attributes, function (e) {
                self.swf = e.ref
                self.swf.trigger = function (...args) {
                    // console.log('swf.trigger', args)
                    // if (args[0] !== 'onEnterFrame') { console.log('player', args) }
                    if (args[0] === 'NetStream.Play.Start') {
                        args[0] = 'play'
                    }
                    self.trigger(args[0], args)
                }
                // self.swf.trigger = self.trigger.bind(self);
            })
        // JavaScript enabled so display the flashContent div in case it is not replaced with a swf object.
    swfobject.createCSS('#' + this.box, 'background-color:#000')
}
Player.prototype.appendVideo = function () {
    var box = this.box
    var self = this
    var $video = document.createElement('video')
    this.playType = 'video'
    $video.setAttribute('autoplay', 'autoplay')
    $video.setAttribute('height', '100%')
    $video.setAttribute('width', '100%')
    $video.setAttribute('id', box)

    $video.setAttribute('x-webkit-airplay', 'allow')
    $video.setAttribute('webkit-playsinline', true)
    $video.setAttribute('playsinline', true)

    document.getElementById(box).replaceWith($video)
    this.$video = $video
    this.$video.addEventListener('timeupdate', this.timeline.bind(this))
    this.$video.addEventListener('ended', this.handlerEnd.bind(this))

    var mediaEvents = {}
    mediaEvents['loadstart'] = 0
    mediaEvents['progress'] = 0
    mediaEvents['suspend'] = 0
    mediaEvents['abort'] = 0
    mediaEvents['error'] = 0
    mediaEvents['emptied'] = 0
    mediaEvents['stalled'] = 0
    mediaEvents['loadedmetadata'] = 0
    mediaEvents['loadeddata'] = 0
    mediaEvents['canplay'] = 0
    mediaEvents['canplaythrough'] = 0
    mediaEvents['playing'] = 0
    mediaEvents['waiting'] = 0
    mediaEvents['seeking'] = 0
    mediaEvents['seeked'] = 0
            // mediaEvents['ended'] = 0
    mediaEvents['durationchange'] = 0
            // mediaEvents['timeupdate'] = 0
    mediaEvents['play'] = 0
    mediaEvents['pause'] = 0
    mediaEvents['ratechange'] = 0
    mediaEvents['resize'] = 0
    mediaEvents['volumechange'] = 0

    Object.keys(mediaEvents).forEach(function (key) {
        $video.addEventListener(key, self.trigger.bind(self, key))
    })
    box = null
    this.trigger('init')
}

Player.prototype.setup = function (opts) {
    this.opts = {
        autostart: 'autostart' in opts ? !!opts.autostart : false,
        muted: opts.muted || false,
        args: {
            uid: window.current_user && (window.current_user.id || 0),
            liveId: window.live_info && (window.live_info.id || '')
        }
            // primary: "html5",
    }
    Object.assign(this.opts, opts)
    if ('swfPath' in opts) {
        this.swfPath = opts.swfPath
    }
    this.type = 'live'
    if ('vodUri' in opts) {
        this.type = 'vod'
        if (opts.vodUri instanceof Array) {
            this.vod = opts.vodUri.length ? opts.vodUri : false
        } else {
            this.vod = [{
                playurl: opts.vodUri
            }]
        }
        if ('totalDuration' in opts) {
            this.totalDuration = opts.totalDuration
        } else if (this.vod && this.vod.length) {
            this.countTotalDuration()
        }
    } else if (sys.mobile && 'hls' in this.opts && this.opts.hls) {
        if (this.opts.hls instanceof Array) {
            this.file = this.opts.hls[0]
        } else {
            this.file = this.opts.hls
        }
    } else if ('rtmp' in this.opts && this.opts.rtmp) {
        if (this.opts.rtmp instanceof Array) {
            this.file = this.opts.rtmp[0]
        } else {
            this.file = this.opts.rtmp
        }
    } else if ('rtmpUri' in opts) {
        this.file = opts.rtmpUri + ''
    } else if ('pullRtmpUrl' in opts) {
        this.file = opts.pullRtmpUrl + ''
    } else {
        this.file = false
    }
        // console.log(opts);
    // return this.opts
    this.inited = false
    if (this.file || this.vod) {
        this.appendPlayer()
    } else {
        console.log('没有找到播放地址')
    }
}
Player.prototype.appendPlayer = function () {
        // var self = this
        // type = type ? type : sys.ios ? 'hls' : 'other';
        // var type = sys.safari || sys.mobile  ? 'hls' : 'other';
        // var url = P.formatUrl('192.168.133.161', live_id, type);
    // this.setup(opts)
    console.log(this.file)
    var type = this.type
    var box = this.box
    if (sys.mobile || (hasVideoFlag && type === 'vod')) {
        this.appendVideo()
    } else if (sys.detachFlash) {
        this.appendSWF()
    } else {
        document.getElementById(box).innerHTML = '系统检测到您的浏览器未开启flash插件。请<a href="http://get.adobe.com/cn/flashplayer/" target="_blank">点击开启</a>'
        swfobject.createCSS('#' + box, 'color: #ccc; text-align: center; font-size: 2em; line-height: 2em; padding-top: 100px')
    }

        // else if (sys.safari || ) {
        // }
    return this
}
Player.prototype.destroy = function () {
    var $box = document.getElementById(this.box)
    $box.replaceWith('<div id="flashContent" style="visibility:hidden"></div>')
}
Player.prototype.getParams = function (call) {
    var params = utils.getParams()
    if (window && window.current_user) {
        params.uid = window.current_user.id || ''
    } else {
        params.uid = ''
    }
    if (window && window.live_info) {
        params.name = window.live_info.id
    }
    params.app = 'live'
    if (typeof this.opts.file === 'string') {
        params.playUri = this.opts.file
    } else if (this.opts.file instanceof Array && this.opts.file.length) {
        params.playUri = this.opts.file[0].mp4
    } else {
        params.playUri = ''
    }
    params.call = call || 'play_done'
    return params
}
Player.prototype.sendOnPlay = function () {
    var params = this.getParams('play')
    utils.sendStat(params)
        // console.log('+++++++++++++++++++++++++')
    params = null
}

Player.prototype.sendOnPlayDone = function () {
    var params = this.getParams('play_done')
    utils.sendStat(params)
}
Player.prototype.getMediaStatistics = function () {
    if (this.playType === 'flash') {
        return this.swf.getMediaStatistics()
    } else {
        return null
    }
}



